/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sub {
  vertical-align: sub;
  font-size: smaller;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

:root {
  scroll-behavior: smooth;
}

html {
  scrollbar-gutter: stable;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #383B45;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  width: 125px;
  height: 125px;
  border: solid 10px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-left-color: #4AF6CD;
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

.nav {
  width: 100%;
  height: 353px;
  margin-bottom: 17px;
}

.nav__list {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 0px;
}

.nav__item {
  width: 100%;
}

.nav__link {
  width: 100%;
  transition: all 0.3s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 6px;
}

.nav__link:hover {
  background-color: #4AF6CD;
  transition: all 0.3s;
}

.nav__svg {
  transition: all 0.3s;
}

.nav__svg:nth-child(1) {
  width: 61px;
  height: 48px;
}

.nav__svg:nth-child(2) {
  width: 52px;
  height: 33px;
}

.nav__svg:nth-child(3) {
  width: 47px;
  height: 49px;
}

.nav__svg:nth-child4(4) {
  width: 25px;
  height: 52px;
}

.nav__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16667;
  letter-spacing: 0.25em;
  text-align: center;
  text-transform: capitalize;
}

.burger {
  display: none;
  position: absolute;
  left: 25px;
  bottom: auto;
  top: 25px;
  width: 28px;
  height: 20px;
  transition: all 0.3s;
  z-index: 10;
  cursor: pointer;
  transition: all 0.5s;
}

.burger-active {
  transform: translateY(calc(100dvh - 80px));
  transition: all 0.5s;
}

.burger:active .burger__span {
  background-color: #4AF6CD;
}

.burger:focus .burger__span {
  background-color: #4AF6CD;
}

.burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #fff;
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
}

.burger__span-two {
  top: 50%;
}

.burger__span-three {
  top: 100%;
}

