  translate: 70px 0 0;
  z-index: 1;
}

.shop .swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.shop__card {
  width: 228px;
  aspect-ratio: 1/3;
  position: relative;
  transition: all 0.3s;
  transform-style: preserve-3d;
  transform: perspective(1000px);
  rotate: y -180deg;
  overflow: hidden;
}

.shop__card:hover {
  rotate: y -180deg;
  transition: all 0.3s;
}

.shop__card--rotated {
  rotate: y 180deg;
}

.shop__card-front {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.shop__card-front-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.shop__card-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  rotate: y 180deg;
}

.shop__card-back-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.shop__card-dot {
  position: absolute;
  top: 200px;
  right: 30px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  outline: 1px solid #4AF6CD;
  background-color: #fff;
  z-index: 10;
}

.shop__card-dot:nth-child(2) {
  top: 200px;
  right: 100px;
}

.shop__card-dot:nth-child(3) {
  top: 200px;
  right: 150px;
}

.shop__card-dot-dash {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50% 0;
  width: 12px;
  height: 2px;
  background-color: #4AF6CD;
}

.shop__card-dot-dash:nth-child(2) {
  rotate: 90deg;
  transition: rotate 0.3s;
}

.shop__card-dot-active .shop__card-dot-dash:nth-child(2) {
  opacity: 0;
  transition: opacity 0.3s;
}

.shop .slider-title__text {
  display: block;
  line-height: 1;
}

/* modals styles*/

.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 15px;
  z-index: -50;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
  background: rgba(0, 0, 0, 0.5);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-window--visible {
  z-index: 50;
  opacity: 1;
  pointer-events: all;
}

